*, *::before, *::after {
    box-sizing: border-box;
}

/* baloo-thambi-2-regular - latin */
@font-face {
    font-family: 'Baloo Thambi 2';
    font-style: normal;
    font-weight: 400;
    src: url('fonts/baloo-thambi-2-v12-latin-regular.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('fonts/baloo-thambi-2-v12-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('fonts/baloo-thambi-2-v12-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('fonts/baloo-thambi-2-v12-latin-regular.woff') format('woff'), /* Modern Browsers */
         url('fonts/baloo-thambi-2-v12-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('fonts/baloo-thambi-2-v12-latin-regular.svg#BalooThambi2') format('svg'); /* Legacy iOS */
  }

body {
    font-size: 14px;
    font-family: 'Baloo Thambi 2';
    position: relative;
}

h1,h2,h3,h4,h5,h6,p,body {
    margin: 0;
    padding: 0;
}

html, body {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cross {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.cross .elem {
    display: inline-block;
    width: 95%;
    height: 4px;
    position: absolute;
}

.elem:first-child {
    transform: rotate(45deg);
}

.elem:nth-child(2) {
    transform: rotate(-45deg);
}

.circle {
    width: 80%;
    height: 80%;
    border-radius: 50%;
}

.tableCell {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tableCell.a {
    cursor: pointer;
}

.tableCell.a:hover {
    opacity: 0.8;
}

.gameLayout {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    z-index: 3;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.layoutBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    opacity: 0.85;
    transition: 0.3s ease-in-out;
}

.layoutBlock:hover {
    transform: scale(1.05);
    opacity: 1;
}


.count {
    
    font-size: 2.2em;
}

.winner {
    font-size: 2em;
}

.retry {
    width: 100px;
    height: 40px;
    border-radius: 10px;
    border: none;
    outline: none;
    font-family: 'Baloo Thambi 2'; 
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4em;
}

.retry:hover {
    opacity: 0.6;
    cursor: pointer;
}

.sunIcon, .moonIcon {
    cursor: pointer;
   
}

.moonTheme, .sunTheme {
    z-index: 3;
}

.sunIcon.dark, .moonIcon.dark {
    filter: invert(100%);
    -webkit-filter: invert(100%);
    -moz-filter: invert(100%);
}

.themeChangeWrapper {
    display: block;
    position: absolute;
    width: 100px;
    
    height: 40px;
    top: 20px;
    left: 50%;
}

.themeChangeLayout {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    position: relative;
    left: -50%;
    overflow: hidden;
    padding: 0 8px;
    border-radius: 8px;
}

.selectedEl {
    position: absolute;
    width: 49px;
    height: 100%;
    z-index: 1;
}